<template>
   <div class="jokewrapper">
       <div class="user">
           <img :src="avatar" alt="" class="avatar">
           <span class="title">
                                {{userName}}
                            </span>
           <ul class="medal-wrapper">
               <li v-for="(medal,i) in medals"class="medal-item">
                   <img :src="medal.small_icon_url" alt="" v-show="medal.count>0" >
               </li>
           </ul>
       </div>
       <div class="joke-content">
           <span class="category-name">#{{categoryName}}#</span>
           <span class="text">{{contentText}}</span>
       </div>
       <slot name="video"></slot>
       <slot name="image"></slot>
       <slot name="gif"></slot>
       <slot name="imagelist"></slot>
   </div>
</template>
<script>
    export default{
        props:[ "avatar", "userName", "medals",  "categoryName", "contentText"],
        data(){
            return{}
        },
    }
</script>
<style lang="stylus">
    .jokewrapper
        overflow: auto
        .joke-item
            font-size: 0
            display:block
        .user
            display:flex
            align-items:center
            font-size: 0
            .title
                display: inline-block
                font-size: 14px
                color: #a9a9a9
            .avatar
                display: inline-block
                width:30px
                height: 30px
                margin: 4px 8px
                border-radius:50%
            .medal-wrapper
                display: inline-block
                .medal-item
                    display:inline-block
                    padding-left:4px
                    img
                        width: 14px
                        height: 14px

        .joke-content
            font-size: 14px
            color: #000
            padding: 2px 10px
            .category-name
                color: #FF69B4
</style>


