<template>
    <div>
        <ul class=" comment-wrapper">
            <li class="digg-count comment-item" >
                <Icon type="thumbsup"></Icon>
                <span>{{diggCount}}</span>
            </li>
            <li class="burycount comment-item">
                <Icon type="thumbsdown"></Icon>
                <span>{{buryCount}}</span>
            </li>
            <li class="comment-count comment-item">
                <Icon type="chatbubble-working"></Icon>
                <span>{{commentCount}}</span>
            </li>
            <li class="share-count comment-item">
                <Icon type="forward"></Icon>
                <span>{{shareCount}}</span>
            </li>
        </ul>
    </div>
</template>
<script>
    export default{
        props:["diggCount","buryCount","commentCount","shareCount"]
    }
</script>
<style lang="stylus">
  .comment-wrapper
      position: relative
      padding: 8px 4px 6px 6px
    .comment-item
      display: inline-block
      margin-right 12px
      font-size: 14px
      color: #A9A9A9
      &.share-count
         position:absolute
         right: 4px
         bottom: 6px
</style>


