<template>
    <div id="home">
            <ul class="homeNav">
                <li v-for="(item,index) in navbar" :key="index" class="navbar-item">
                    <router-link :to="{path:item.url,query:{type:item.type}}">{{item.text}}</router-link>
                </li>
            </ul>
        <keep-alive>
            <router-view></router-view>
        </keep-alive>

        <bottom-nav></bottom-nav>

    </div>
</template>
<script>
    import Add from '../bottomNav/Add.vue'
    import  Discover from '../bottomNav/Add.vue'
    import Admin from '../bottomNav/Add.vue'
    import DuanyouShow from '../bottomNav/DuanyouShow.vue'
    import focus from '../focus/focus.vue'
    import game from '../game/game.vue'
    import joke from '../joke/joke.vue'
    import live from '../live/live.vue'
    import pic from '../picture/pic.vue'
    import recommend from '../recommend/recommend.vue'
    import vedioShow from '../vedioShow/vedioShow.vue'
    import essence from '../essence/essence.vue'
    import bottomNav from '../bottomNav/Bottom-nav.vue'

    export default{
        data() {
            return {
                navbar: [
                    {
                        text: '关注',
                        url: '/home/fucus',
                    },
                    {
                        text: '推荐',
                        url: '/home/recommend',
                    },
                    {
                        text: '视频',
                        url: '/home/vedioShow',
                    },
                    {
                        text: '直播',
                        url: '/home/live',
                    },
                    {
                        text: '图片',
                        url: '/home/pic',
                    },
                    {
                        text: '段子',
                        url: '/home/joke',
                    },
                    {
                        text: '精华',
                        url: '/home/essence',
                    },
                    {
                        text: '游戏',
                        url: '/home/game',
                    }]
            }
        },
        components:{
            bottomNav,
            essence,
            focus,
            game,
            joke,
            live,
            pic,
            recommend,
            vedioShow
        },
        beforeRouteUpdate (to, from, next){
            next();
        },

    }
</script>
<style lang="stylus">
        .homeNav
          width: 100%
          overflow: hidden;
          overflow-x: auto
          text-align: center
          position: fixed
          margin-bottom: 6px;
          left: 0
          font-size: 0
          background: #fff
          font-family: '微软雅黑'
          white-space: nowrap
          z-index: 999
          .navbar-item
            display: inline-block
            height: 1rem;
            line-height: 1rem;
            width: 1.4rem;
            padding: 4px  8px
            color: #dcdcdc
            font-size: 18px
            & a
              color: #c0c0c0
            .router-link-active
              color: #000
              border-bottom: 4px solid #FF69B4;
        ::-webkit-scrollbar
          display: none

</style>


