<template>
    <div id="recommend">
        <ul>
            <li v-for="(item,index) in recommendList"   >
                <userContent
                        :avatar="item.group.user.avatar_url"
                        :user-name="item.group.user.name"
                        :medals="item.group.user.medals"
                        :category-name="item.group.category_name"
                        :content-text="item.group.text">


                   <video :src="item.group.gifvideo.mp4_url"  v-if="item.group.media_type===2" controls="controls" slot="gif"></video>
                   <video :src="item.group.mp4_url"  v-if="item.group.media_type===3" controls="controls" slot="video"></video>
                   <div slot="image"v-if="item.group.media_type===1" >
                    <img :src="item.group.large_image.url_list[0].url" alt="">
                   </div>
                </userContent>
                <commentBar :digg-count="item.group.digg_count" :bury-count="item.group.bury_count" :comment-count="item.group.comment_count" :share-count="item.group.share_count">
                </commentBar>
                <bigBorder></bigBorder>

            </li>
        </ul>

    </div>
</template>
<script>
    const recommend="-101"
    import api from "../../api/"
    import jsonp from 'jsonp'
    import userContent from '../public/userContent.vue'
    import commentBar from '../public/commentBar.vue'
    import bigBorder from "../public/bigBorder.vue"
    export default{
        data(){
            return{
                recommendList:[]
            }
        },
        mounted (){
            this.get()

        },
        methods:{
            get()
            {
                let _this=this;
                jsonp(api.getdata(recommend),function (err,res) {
                        _this.recommendList=res.data.data
                    }
                )
            }
        },
        components:{
            userContent,
            commentBar,
            bigBorder
        },

    }
</script>
<style lang="stylus">
   #recommend
     position: relative
     top: 40px

</style>


