<template>
    <div id="joke">
        <div class="jokewrapper">
            <div>
                <ul>
                    <router-link   class="joke-item" v-for="(item,index) in jokeList" :to="{ name:'jokeDetailShow',  params:{
                        id:item.group.id,
                        avatar:item.group.user.avatar_url,
                        username:item.group.user.name,
                        works:item.group.user.ugc_count,
                        fans:item.group.user.followers,
                        medal:item.group.user.medals,
                        diggCount:item.group.digg_count,
                        buryCount:item.group.bury_count,
                        commentCount:item.group.comment_count,
                        shareCount: item.group.share_count,
                        categoryName:item.group.category_name,
                        contentText:item.group.text
                    }}" :key="index">

                            <userContent
                                          :avatar="item.group.user.avatar_url"
                                          :user-name="item.group.user.name"
                                          :medals="item.group.user.medals"
                                          :category-name="item.group.category_name"
                                          :content-text="item.group.text">

                            </userContent>
                            <commentBar :digg-count="item.group.digg_count" :bury-count="item.group.bury_count" :comment-count="item.group.comment_count" :share-count="item.group.share_count">
                            </commentBar>
                            <bigborder></bigborder>
                    </router-link>
                </ul>
            </div>
        </div>
    </div>


</template>
<script>
   const joke ="-102"
   import api from "../../api/"
   import jsonp from 'jsonp'
   import bigborder from "../public/bigBorder.vue"
   import commentBar from "../public/commentBar.vue"
   import userContent from "../public/userContent.vue"
   export default
   {
       name: 'joke',
       data (){
           return {
                jokeList:[]
           }
       },
       mounted (){
            this.get()

       },
       methods:{
           get()
           {
               let _this=this;
               jsonp(api.getdata(joke),function (err,res) {
                     _this.jokeList=res.data.data
                 }
               )
           }
       },
       components:{
           bigborder,
           commentBar,
           userContent
       }
   }
</script>
<style lang="stylus">
    #joke
      position: relative
      top: 40px

</style>


