import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/bottomNav/Home.vue'
import DuanyouShow from '../components/bottomNav/DuanyouShow.vue'
import Add from '../components/bottomNav/Add.vue'
import  Discover from '../components/bottomNav/Discover.vue'
import Admin from '../components/bottomNav/Admin.vue'
import focus from '../components/focus/focus.vue'
import game from '../components/game/game.vue'
import joke from '../components/joke/joke.vue'
import live from '../components/live/live.vue'
import pic from '../components/picture/pic.vue'
import recommend from '../components/recommend/recommend.vue'
import vedioShow from '../components/vedioShow/vedioShow.vue'
import essence from '../components/essence/essence.vue'
import jokeDetailShow from '../components/joke/jokeDetailShow.vue'

Vue.use(Router);

const routes = [{
    path: '/home',
    component: Home,
    name: 'home',
    children:[
        { path: '/home/focus', component: focus},
        { path: '/home/game', component: game},
        { path: '/home/joke', component: joke},
        { path: '/home/live', component: live},
        { path: '/home/pic', component: pic},
        { path: '/home/recommend', component: recommend},
        { path: '/home/vedioShow', component: vedioShow},
        { path: '/home/essence', component: essence}]
    },
    {
        path: '/duanyouShow',
        component: DuanyouShow,
        name: 'duanyouShow'
    },
    {
        path: '/add',
        component: Add,
        name: 'add'
    },
    {
        path: '/discover',
        component: Discover,
        name: 'discover'
    },
    {
        path: '/admin',
        component: Admin,
        name: 'admin'
    },
    {
        path:'/jokeDetailShow/:id',
        component:jokeDetailShow,
        name:'jokeDetailShow'
    },
    {
        path: '',
        redirect: '/home/focus'
    },
    {
        path: '*',
        redirect: '/home/focus'
    },

];
const scrollBehavior = (to, from, savedPosition) => {
    if(savedPosition){
        return savedPosition
    }else{
        return { x: 0, y: 0 }
    }
}
const router = new Router({
    mode: 'history',
    routes,
    scrollBehavior

})
export default router


