<template>
  <div id="jokeDetailShow">
      <div class="headBar">
        <Icon type="chevron-left" @click.native="goBack"></Icon>
        <img :src="avatar" alt=""class="avatar">
        <div class="user">
          <div class="username">
            <div class="name-text">
              {{username}}
            </div>
            <div class="information">
              <span>{{worksAmount}}作品</span>
              <span>{{fansAmount}}粉丝</span>
            </div>
          </div>
        </div>
        <ul class="medal-wrapper">
          <li v-for="(medal,i) in  medals"class="medal-item" :key="i">
              <img :src="medal.small_icon_url" alt="" v-show="medal.count>0" >
          </li>
      </ul>
        <div class="add-focus">
            +关注
         </div>
        <Icon type="android-more-vertical"></Icon>
      </div>

      <div class="joke-content">
            <span class="category-name">
                #{{categoryName}}#
            </span>
              <span class="text">
             {{contentText}}
            </span>
       </div>
      <commentBar :digg-count="diggCount" :bury-count=" buryCount" :comment-count="commentCount" :share-count="shareCount">
      </commentBar>
      <commentArea></commentArea>

  </div>
</template>
<script>
   import commentBar from "../public/commentBar.vue"
   import commentArea from "../public/commentArea.vue"
   export default{
       data(){
           return {
               avatar:'',
               username:'',
               worksAmount:'',
               fansAmount:'',
               medals:'',
               diggCount:'',
               buryCount:'',
               commentCount:'',
               shareCount:'',
               categoryName:'',
               contentText:''
           }
       },
       methods:{
         goBack(){
             history.back()
         },
         getData(){
             this.avatar=this.$route.params.avatar,
             this.username=this.$route.params.username,
             this.worksAmount=this.$route.params.works,
             this.fansAmount=this.$route.params.fans,
             this. medals=this.$route.params.medal,
             this. diggCount=this.$route.params.diggCount,
             this. buryCount=this.$route.params.buryCount,
             this.commentCount=this.$route.params.commentCount,
             this.shareCount=this.$route.params.shareCount,
             this.categoryName=this.$route.params.categoryName,
             this.contentText=this.$route.params.contentText
         }
       },
       created(){
               this.getData();
       },
       components:{
           commentBar,
           commentArea
       },
       watch: {
           '$route':'getData'
       }
   }
</script>
<style lang="stylus">
    .headBar
      position:relative
      display:flex
      align-items:center
      padding :4px 14px 4px 0
      border-bottom: 1px solid #F5F5F5
      .ivu-icon-chevron-left
        padding:0 14px 0 14px
        font-size: 16px
      .avatar
        display: inline-block
        width: 32px
        height: 32px
        border-radius: 50%
        margin-right: 8px
      .user
        display: inline-block
        .username
          .name-text
            font-size: 14px
      .medal-wrapper
        display: inline-block
        margin-top: -16px
        .medal-item
          display: inline-block
          img
            width: 14px
            height: 14px
      .add-focus
          display:inline-block
          width:48px
          margin-left: 12px
          line-height:20px
          color: #FF69B4
          text-align:center
          border: 1px solid  #FF69B4
          border-radius: 4px
      .ivu-icon-android-more-vertical
        position:absolute
        right: 0px
        padding: 8px 14px 8px 14px
        font-size:24px
    .joke-content
      font-size: 14px
      color: #000
      padding: 2px 10px
      .category-name
       color: #FF69B4





</style>


